import React, { useState, useEffect } from 'react';
import './App.css';
import { supabase } from './supabaseClient';
import Auth from './components/Auth';
import Dashboard from './components/Dashboard';
import StartScreen from './components/StartScreen';
import QuizScreen from './components/QuizScreen';
import ResultsScreen from './components/ResultsScreen';

function App() {
  const [session, setSession] = useState(null);
  const [screen, setScreen] = useState('dashboard');
  const [quizConfig, setQuizConfig] = useState({ category: '', difficulty: '' });
  const [score, setScore] = useState(0);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    supabase.auth.getSession().then(({ data: { session } }) => {
      setSession(session);
      setLoading(false);
    });

    const { data: { subscription } } = supabase.auth.onAuthStateChange((_event, session) => {
      setSession(session);
      if (session) {
        setScreen('dashboard');
      }
    });

    return () => subscription.unsubscribe();
  }, []);

  const startQuiz = (category, difficulty) => {
    setQuizConfig({ category, difficulty });
    setScore(0);
    setScreen('quiz');
  };

  const endQuiz = async (finalScore, answers, categoryName) => {
    setScore(finalScore);

    if (session) {
      // Save game
      const { data: gameData, error: gameError } = await supabase
        .from('games')
        .insert([{
          user_id: session.user.id,
          category: categoryName,
          difficulty: quizConfig.difficulty,
          score: finalScore,
          total_questions: answers.length
        }])
        .select()
        .single();

      if (gameError) {
        console.error('Error saving game:', gameError);
      } else if (gameData && answers.length > 0) {
        // Save individual answers
        const answerRows = answers.map(a => ({
          game_id: gameData.id,
          user_id: session.user.id,
          question_text: a.question_text,
          correct_answer: a.correct_answer,
          user_answer: a.user_answer,
          is_correct: a.is_correct
        }));

        const { error: answersError } = await supabase
          .from('game_answers')
          .insert(answerRows);

        if (answersError) console.error('Error saving answers:', answersError);
      }
    }

    setScreen('results');
  };

  const restartQuiz = () => {
    setScreen('dashboard');
  };

  if (loading) {
    return (
      <div className="App">
        <div style={{ color: 'white', fontSize: '1.5rem' }}>Loading...</div>
      </div>
    );
  }

  if (!session) {
    return (
      <div className="App">
        <Auth />
      </div>
    );
  }

  return (
    <div className="App">
      {screen === 'dashboard' && (
        <Dashboard
          user={session.user}
          onStartQuiz={() => setScreen('start')}
        />
      )}
      {screen === 'start' && (
        <StartScreen onStart={startQuiz} />
      )}
      {screen === 'quiz' && (
        <QuizScreen
          category={quizConfig.category}
          difficulty={quizConfig.difficulty}
          onEnd={endQuiz}
        />
      )}
      {screen === 'results' && (
        <ResultsScreen
          score={score}
          onRestart={restartQuiz}
        />
      )}
    </div>
  );
}

export default App;
