import React, { useState, useEffect } from 'react';
import { supabase } from '../supabaseClient';
import './Dashboard.css';

function Dashboard({ user, onStartQuiz }) {
  const [games, setGames] = useState([]);
  const [profile, setProfile] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchProfile();
    fetchGames();
  }, []);

  const fetchProfile = async () => {
    const { data } = await supabase
      .from('profiles')
      .select('*')
      .eq('id', user.id)
      .single();
    setProfile(data);
  };

  const fetchGames = async () => {
    const { data } = await supabase
      .from('games')
      .select('*')
      .eq('user_id', user.id)
      .order('created_at', { ascending: false });
    setGames(data || []);
    setLoading(false);
  };

  const handleLogout = async () => {
    await supabase.auth.signOut();
  };

  const getStats = () => {
    if (games.length === 0) return null;
    const totalGames = games.length;
    const avgScore = (games.reduce((sum, g) => sum + g.score, 0) / totalGames).toFixed(1);
    const bestScore = Math.max(...games.map(g => g.score));
    const totalCorrect = games.reduce((sum, g) => sum + g.score, 0);
    return { totalGames, avgScore, bestScore, totalCorrect };
  };

  const stats = getStats();

  return (
    <div className="dashboard">
      <div className="dashboard-header">
        <div className="welcome">
          <h1>Welcome back, {profile?.username || user.email}!</h1>
          <p>Ready to test your knowledge?</p>
        </div>
        <button className="logout-btn" onClick={handleLogout}>Logout</button>
      </div>

      {stats && (
        <div className="stats-grid">
          <div className="stat-card">
            <div className="stat-number">{stats.totalGames}</div>
            <div className="stat-label">Games Played</div>
          </div>
          <div className="stat-card">
            <div className="stat-number">{stats.avgScore}/10</div>
            <div className="stat-label">Average Score</div>
          </div>
          <div className="stat-card">
            <div className="stat-number">{stats.bestScore}/10</div>
            <div className="stat-label">Best Score</div>
          </div>
          <div className="stat-card">
            <div className="stat-number">{stats.totalCorrect}</div>
            <div className="stat-label">Total Correct</div>
          </div>
        </div>
      )}

      <button className="play-btn" onClick={onStartQuiz}>
        Start New Quiz
      </button>

      <div className="recent-games">
        <h2>Recent Games</h2>
        {loading ? (
          <p>Loading...</p>
        ) : games.length === 0 ? (
          <p className="no-games">No games yet. Start playing!</p>
        ) : (
          <div className="games-list">
            {games.slice(0, 10).map((game) => (
              <div key={game.id} className="game-entry">
                <div className="game-info">
                  <span className="game-category">{game.category}</span>
                  <span className="game-difficulty">{game.difficulty}</span>
                </div>
                <div className="game-score">{game.score}/{game.total_questions}</div>
                <div className="game-date">
                  {new Date(game.created_at).toLocaleDateString()}
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
}

export default Dashboard;
