import React, { useState, useEffect } from 'react';
import { supabase } from '../supabaseClient';
import './ResultsScreen.css';

function ResultsScreen({ score, onRestart }) {
  const [leaderboard, setLeaderboard] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    fetchLeaderboard();
  }, []);

  const fetchLeaderboard = async () => {
    try {
      const { data, error } = await supabase
        .from('games')
        .select('score, user_id, profiles(username)')
        .order('score', { ascending: false })
        .limit(10);

      if (error) {
        console.error('Leaderboard error:', error);
        setError(error.message);
      } else {
        setLeaderboard(data || []);
      }
    } catch (err) {
      console.error('Fetch error:', err);
      setError(err.message);
    }
    setLoading(false);
  };

  const getPerformanceMessage = () => {
    if (score === 10) return ' Perfect Score! Incredible!';
    if (score >= 8) return ' Excellent work!';
    if (score >= 6) return ' Good job!';
    if (score >= 4) return ' Not bad, keep practicing!';
    return ' Try again, you can do better!';
  };

  return (
    <div className="results-screen">
      <h1>Quiz Complete!</h1>

      <div className="score-display">
        <div className="final-score">{score}/10</div>
        <p className="performance-message">{getPerformanceMessage()}</p>
        <p className="score-saved"> Score saved to your profile!</p>
      </div>

      <div className="leaderboard">
        <h2> Top Scores</h2>
        {loading ? (
          <p className="no-scores">Loading leaderboard...</p>
        ) : error ? (
          <p className="no-scores">Could not load leaderboard: {error}</p>
        ) : leaderboard.length === 0 ? (
          <p className="no-scores">No scores yet. Be the first!</p>
        ) : (
          <div className="leaderboard-list">
            {leaderboard.map((entry, index) => (
              <div key={index} className={`leaderboard-entry rank-${index + 1}`}>
                <span className="rank">#{index + 1}</span>
                <span className="name">{entry.profiles?.username || 'Anonymous'}</span>
                <span className="entry-score">{entry.score}/10</span>
              </div>
            ))}
          </div>
        )}
      </div>

      <div className="results-actions">
        <button className="restart-btn" onClick={onRestart}>
           Play Again
        </button>
      </div>
    </div>
  );
}

export default ResultsScreen;
